import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
const SELECTED_BAGS = {
  campus: "The Campus",
  daily: "The Daily",
  bagpacker: "The Bagpacker",
};

@Component({
  selector: 'app-bag-image',
  templateUrl: './bag-image.component.html',
  styleUrls: ['./bag-image.component.css']
})
export class BagImageComponent implements OnInit {
  leftBag={color:"",material:"",type:""};
  selected:String;
  constructor(private _route:ActivatedRoute, private _router:Router) {
 }

  ngOnInit() {
    this.sub=this._route.params.subscribe(params=>{
      this.bag=params.slug;
      this.bagNice=SELECTED_BAGS[params.slug];
    })
  }
  bagNice:String;
  sub:any;
  bag:String;
  existingMods={lb:{color:"blue",material:"lether"},rb:{color:"red",material:"shaumstoff"}};
  existingQuery="";
  myImg:string="/assets/img/example.png";

  mod(whichOne:String){
    this.selected=whichOne;
    this.search();
  }

  search() {
  this._router.navigate(["modify/"+this.bag, {
    selected:this.selected,
  }]);
}
}
