import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
const SELECTED_BAGS = {
  campus: "The Campus",
  daily: "The Daily",
  bagpacker: "The Bagpacker",
};

@Component({
  selector: 'app-modify-bag',
  templateUrl: './modify-bag.component.html',
  styleUrls: ['./modify-bag.component.css']
})
export class ModifyBagComponent implements OnInit {


  constructor(private _route:ActivatedRoute) { }

  ngOnInit() {
    this.sub=this._route.params.subscribe(params=>{
      this.bagNice=SELECTED_BAGS[params["slug"]];
    })
  }
  sub:any;
  bag:string;
  bagNice:string;
  myImg:string="/assets/img/example.png";

}
