@Component({
  selector: 'side-navbar',
  templateUrl: './side-navbar.component.html',
  styleUrls: ['./side-navbar.component.css']
})


export class SideNavbarComponent implements OnInit {


  constructor(private _route: ActivatedRoute, private _router: Router) {
  }

  ngOnInit() {
    this.sub = this._route.params.subscribe(params => {
      this.selected = params["selected"];
      this.selectedNice = SELECTED_TYPES[params["selected"]];
      this.bag = params.slug;
      this.radios = SELECTED_RADIO[params["selected"]];
      this.existingMod=params["mod"];
    })
  }
  existingMod:string;
  radios: any;
  bag: string;
  selected: string;
  selectedNice: string;
  sub: any;
  material: string;
  selectedValues = {material: "", color: "", gadget: "", letter: "", closing: ""};

  addGadget(value) {
    this.selectedValues.gadget = value;
    this.navigate();
  }

  addMaterial(value) {
    this.selectedValues.material = value;
    this.navigate();
  }

  addColor(value) {
    this.selectedValues.color = value;
    this.navigate();
  }

  addClosing(value) {
    this.selectedValues.closing = value;
    this.navigate();
  }

  addLetter(value) {
    this.selectedValues.letter = value;
    this.navigate();
  }
  // returns some_name-material=lether&
  makeSlug(){
    let slug="";
    Object.entries(this.selectedValues).forEach(
      ([key, value]) => {
        if(value!=""){
          slug+=this.selected+"-"+key+"%"+value+"&";
        }
      }
    );
  return slug;
  }

  navigate() {
    let mod=this.makeSlug();
    this._router.navigate(["modify/" + this.bag, {
      selected: this.selected,
      mod:mod

    }]);

  }


}
