import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";


const SELECTED_TYPES = {
  left_bag: "Linke Seitentasche",
  right_bag: "Rechte Seitentasche",
  top_bag: "Obere vordere Tasche",
  bottom_bag: "Untere vordere Tasche"
};

const SELECTED_BAGS = {
  campus: "The Campus",
  daily: "The Daily",
  bagpacker: "The Bagpacker",
};

@Component({
  selector: 'app-show-costs',
  templateUrl: './show-costs.component.html',
  styleUrls: ['./show-costs.component.css']
})
export class ShowCostsComponent implements OnInit {

  constructor(private _route: ActivatedRoute) {
  }

  ngOnInit() {
    this.sub = this._route.params.subscribe(params => {
      this.bag=SELECTED_BAGS[params.slug];
      this.getCurrentMods(params["mod"]);
    });

  }
  getCurrentMods(mod){
    if(mod){
    this.splitted = (mod.split("&"));

    this.splitted.map(el => {

      let minusSplit = el.split("-");
      let andSplit=[];
      if (minusSplit[1]) {
        andSplit= minusSplit[1].split("&");
      }
      let percSplit=[];
      if (andSplit[0]){
        percSplit = andSplit[0].split("%");
      }
      let bagKey=minusSplit[0];
      let typeKey=percSplit[0];
      if(bagKey&&typeKey&&percSplit[1]){
        this.mods[bagKey][typeKey]=percSplit[1];
      }
    })
    }

  }

  isEmpty(obj) {
    for(var key in obj) {
      if(obj.hasOwnProperty(key))
        return false;
    }
    return true;
  }


  sub: any;
  splitted: any;
  bag:any;
  mods = {
    left_bag: {gadget: "", color: "", material: "", letter: "", closing: ""},
    right_bag: {gadget: "", color: "", material: "", letter: "", closing: ""},
    bottom_bag: {gadget: "", color: "", material: "", letter: "", closing: ""},
    top_bag: {gadget: "", color: "", material: "", letter: "", closing: ""}
  }

}
